.page-body {
  min-height: 100%;
  margin: 0 auto;
  padding: 0;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  color: $special_dark-grey;
  background-color: $white;
}

@media (min-width: $desktop-width) {
  .page-body {
    position: relative;
  }
}
